.sectionBanner {
    margin: 0 0 100px 0;

    .sectionInner {}

    &_banner {
        display       : flex;
        align-items   : flex-start;
        flex-direction: column;
        background    : transparent url(../img/banner.png) 50% 50%/cover;
        width         : 100%;
        height        : 440px;
        border-radius : 16px;
        padding       : 64px;

        &_tags {
            &_item {
                width : 160px;
                height: 40px;
                @include RelewaySemiBold(16);
                line-height  : 24px;
                color        : $colorWhite;
                border       : 1px solid $colorWhite;
                border-radius: 100px;
                padding      : 8px 24px;
                margin       : 0 16px 0 0;

                &:last-child {
                    margin: 0;
                }
            }

            margin: 0 0 24px 0;
        }

        &_title {
            @include RelewayBold(72);
            line-height: 80px;
            color      : $colorWhite;
            margin     : 0 0 8px 0;
        }

        &_subTitle {
            @include RelewayRegular(24);
            line-height: 32px;
            color      : $colorWhite;
            margin     : 0 0 40px 0;
        }

        &_btn {}
    }


}