.sectionCategory {
    background-color: $colorWhite;
    padding         : 0 0 64px 0;

    .sectionInner {
        background-color: $colorPrimary;

        display        : flex;
        flex-direction : row;
        align-items    : center;
        justify-content: space-between;
        border-radius  : 8px;
    }



    &_info {
        padding        : 40px;
        display        : flex;
        flex-direction : column;
        align-items    : flex-start;
        justify-content: space-between;
        color          : $colorWhite;

        &_title {
            @include RelewaySemiBold(32);
            line-height: 40px;
            margin     : 0 0 16px 0;
        }

        &_text {
            @include RelewayRegular(18);
            line-height: 22px;
            margin     : 0 0 40px 0;
        }

        &_btn {}

        .btn {}

        .white {
            background-color: $colorWhite;
            color           : $colorTextDark;
            @include RelewaySemiBold(16);
            line-height: 24px;
        }
    }

    &_offer {
        border-radius   : 8px;
        width           : 50%;
        flex-shrink     : 0;
        background-color: $colorBgBlockSecondary;
        display         : flex;
        flex-direction  : column;
        align-items     : center;
        justify-content : center;
        position        : relative;

        &_label {
            @include RelewaySemiBold(12);
            line-height     : 16px;
            background-color: $colorTextSecondary;
            color           : $colorWhite;
            padding         : 12px 24px;
            position        : absolute;
            top             : 40px;
            left            : 60px;
        }

        &_img {}

        &_price {
            @include RelewaySemiBold(32);
            line-height: 40px;

            transform       : skew(160deg);
            background-color: $colorGreen;
            color           : $colorTextDark;
            padding         : 8px 20px;
            display         : flex;
            flex-direction  : column;
            position        : absolute;
            bottom          : 25px;
            right           : 100px;


            .oldPrice {
                @include RelewaySemiBold(12);
                line-height    : 16px;
                text-decoration: line-through;
                align-self     : flex-end;
            }

            span {
                display  : block;
                transform: skew(-160deg);

                &::after {
                    content: '₽';
                }
            }
        }
    }
}