@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  background-color: #F7F7FC;
}

.sectionInner {
  width: 1360px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

.btn {
  padding: 16px 24px;
  background-color: #EE131B;
  outline: none;
  border: none;
  border-radius: 8px;
  color: #fff;
  white-space: nowrap;
}

.title {
  font-family: "Releway", sans-serif;
  font-weight: 600;
  font-size: 48px;
  font-style: normal;
  line-height: 56px;
  color: #000;
  margin: 0 0 40px 0;
}

.breadcrumbs {
  margin: 0 0 40px 0;
}
.breadcrumbs .sectionInner {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.breadcrumbsMain {
  display: block;
  width: 18px;
  height: 18px;
  background: transparent url(../img/home.svg) 50% 50%/cover no-repeat;
}
.breadcrumbsElement {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  color: #A4ABB5;
  font-family: "Releway", sans-serif;
  font-weight: 400;
  font-size: 12px;
  font-style: normal;
  line-height: 16px;
}
.breadcrumbsElement::before {
  content: "";
  display: block;
  width: 4px;
  height: 4px;
  background: transparent url(../img/dot.svg) 50% 50%/cover no-repeat;
  margin: 0 8px;
}

.tooltip {
  position: relative;
  display: inline-block;
}

/* Tooltip text */
.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 1;
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext {
  visibility: visible;
}

.header_secondLine_burger {
  top: 0;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 80px 0 0;
}
.header_secondLine_burger .burger {
  margin-top: 20px;
  width: 100px;
  height: auto;
  border: none;
}
.header_secondLine_burger .burger-menu_button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 16px 24px;
  border-radius: 8px;
  top: 0;
  right: 0;
  z-index: 100;
  -webkit-transition: 0.4s;
  -moz-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
  background-color: #EE131B;
  cursor: pointer;
  font-family: "Releway", sans-serif;
  font-weight: 500;
  font-size: 18px;
  font-style: normal;
  line-height: 24px;
}
.header_secondLine_burger .burger-menu_button span {
  color: white;
  margin: 0 0 0 16px;
}
.header_secondLine_burger .burger-menu_button:hover .burger-menu_lines {
  filter: brightness(0.7);
}
.header_secondLine_burger .burger-menu_nav {
  padding: 30px 40px;
  position: absolute;
  z-index: 70;
  display: none;
  flex-direction: row;
  background-color: #fff;
  overflow-y: auto;
  height: 415px;
  bottom: 0px;
  left: 0px;
  -webkit-transition: 0.8s;
  -moz-transition: 0.8s;
  -o-transition: 0.8s;
  transition: 0.8s;
  opacity: 0;
  flex: 1;
  visibility: hidden;
  font-family: "Releway", sans-serif;
  font-weight: 500;
  font-size: 18px;
  font-style: normal;
  line-height: 24px;
  width: 100vw;
}
.header_secondLine_burger .burger-menu_nav .sectionInner {
  overflow-x: hidden;
}
.header_secondLine_burger .burger-menu_active .burger-menu_nav {
  display: flex;
  opacity: 1;
  visibility: visible;
  top: 105px;
  -webkit-transition: 0.4s;
  -moz-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
}
.header_secondLine_burger .categoryContainer {
  display: grid;
  grid-template-columns: repeat(3, 300px);
  gap: 32px;
  height: 100%;
}
.header_secondLine_burger .categoryContainer .categoryContainer_item {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.header_secondLine_burger .categoryContainer .categoryContainer_item .categoryContainer_img {
  border-radius: 16px;
  margin: 0 16px 0 0;
}
.header_secondLine_burger .categoryContainer .categoryContainer_item .categoryContainer_list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
}
.header_secondLine_burger .categoryContainer .categoryContainer_item .categoryContainer_list_title {
  font-family: "Releway", sans-serif;
  font-weight: 600;
  font-size: 16px;
  font-style: normal;
  line-height: 24px;
  color: #2B2B2B;
  margin: 0 0 12px 0;
}
.header_secondLine_burger .categoryContainer .categoryContainer_item .categoryContainer_list_item {
  font-family: "Releway", sans-serif;
  font-weight: 500;
  font-size: 12px;
  font-style: normal;
  line-height: 16px;
  color: #2B2B2B;
  margin: 0 0 4px 0;
}
.header_secondLine_burger .categoryContainer .categoryContainer_item .categoryContainer_list_item:last-child {
  margin: 0 0 12px 0;
}
.header_secondLine_burger .categoryContainer .categoryContainer_item .categoryContainer_list_showAll {
  font-family: "Releway", sans-serif;
  font-weight: 600;
  font-size: 12px;
  font-style: normal;
  line-height: 16px;
  color: #2B2B2B;
}
.header_secondLine_burger .burger_img {
  border-radius: 16px;
  width: 315px;
  height: 350px;
}
.header_secondLine_burger .burger-menu_link {
  font-family: Raleway, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 120%;
  text-decoration: none;
  letter-spacing: 0.1em;
  color: #000;
  height: 50px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
