}
.header_secondLine_burger .burger-menu_link:hover {
  color: #000;
}
.header_secondLine_burger .burger-menu_overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 50;
}
.header_secondLine_burger .burger-menu_active .burger-menu_overlay {
  display: block;
  background-color: rgba(0, 0, 0, 0.5);
}

