.header_secondLine_burger {
    top            : 0;
    z-index        : 100;
    display        : flex;
    align-items    : center;
    justify-content: center;
    margin         : 0 80px 0 0;

    .burger {
        margin-top: 20px; //paste your styles 
        width     : 100px; //paste your styles
        height    : auto;
        border    : none;
    }

    .burger-menu_button {
        display           : flex;
        align-items       : center;
        justify-content   : center;
        padding           : 16px 24px;
        border-radius     : 8px;
        top               : 0;
        right             : 0;
        z-index           : 100;
        -webkit-transition: 0.4s;
        -moz-transition   : 0.4s;
        -o-transition     : 0.4s;
        transition        : 0.4s;
        background-color  : $colorPrimary; //paste your styles
        cursor            : pointer;
        @include RelewayMedium(18);
        line-height: 24px;

        span {
            color : white;
            margin: 0 0 0 16px;
        }


        &_active {
            // right: 75);  
        }
    }

    .burger-menu_button:hover .burger-menu_lines {
        filter: brightness(0.7);
    }

    .burger-menu_nav {
        padding              : 30px 40px; //paste your styles 
        position             : absolute;
        z-index              : 70;
        display              : none;
        flex-direction       : row;
        background-color     : #fff;
        overflow-y           : auto;
        height               : 415px; //paste your styles 
        bottom               : 0px; //paste your styles 
        left                 : 0px;
        -webkit-transition   : 0.8s;
        -moz-transition      : 0.8s;
        -o-transition        : 0.8s;
        transition           : 0.8s;
        opacity              : 0;
        flex                 : 1;
        visibility           : hidden;
        // flex-wrap         : wrap;
        @include RelewayMedium(18);
        line-height: 24px;
        width      : 100vw;

        .sectionInner {
            overflow-x: hidden;
        }
    }

    .burger-menu_active .burger-menu_nav {
        display           : flex;
        opacity           : 1;
        visibility        : visible;
        top               : 105px; //paste your styles 
        -webkit-transition: 0.4s;
        -moz-transition   : 0.4s;
        -o-transition     : 0.4s;
        transition        : 0.4s;
    }


    .categoryContainer {
        display              : grid;
        grid-template-columns: repeat(3, 300px);
        gap                  : 32px;
        height: 100%;

        .categoryContainer_item {
            display        : flex;
            flex-direction : row;
            align-items    : flex-start;
            justify-content: flex-start;

            .categoryContainer_img {
                border-radius: 16px;
                margin       : 0 16px 0 0;
            }

            .categoryContainer_list {
                display        : flex;
                flex-direction : column;
                align-items    : flex-start;
                justify-content: space-between;
            }

            .categoryContainer_list_title {
                @include RelewaySemiBold(16);
                line-height: 24px;
                color      : $colorTextPrimary;
                margin     : 0 0 12px 0;
            }

            .categoryContainer_list_item {
                @include RelewayMedium(12);
                line-height: 16px;
                color      : $colorTextPrimary;
                margin     : 0 0 4px 0;

                &:last-child {
                    margin: 0 0 12px 0;
                }
            }

            .categoryContainer_list_showAll {
                @include RelewaySemiBold(12);
                line-height: 16px;
                color      : $colorTextPrimary;
            }

        }

    }

    .burger_img {
        border-radius: 16px;
        width        : 315px;
        height       : 350px;
    }

    .burger-menu_link {
        font-family       : Raleway, sans-serif; //paste your styles
        font-style        : normal; //paste your styles
        font-weight       : 400; //paste your styles
        font-size         : 22px; //paste your styles
        line-height       : 120%; //paste your styles
        text-decoration   : none;
        letter-spacing    : 0.1em;
        color             : #000;
        // width          : 100%;
        height            : 50px; //paste your styles
        display           : flex;
        justify-content   : flex-start;
        align-items       : center;
        text-transform    : uppercase;
    }

    .burger-menu_link:hover {
        color: #000; //paste your styles 
    }

    .burger-menu_overlay {
        display : none;
        position: fixed;
        top     : 0;
        left    : 0;
        width   : 100vw;
        height  : 100vh;
        z-index : 50;
    }

    .burger-menu_active .burger-menu_overlay {
        display         : block;
        background-color: rgba(0, 0, 0, 0.5);
    }
}

.header {
    width           : 100%;
    background-color: #fff;
    margin          : 0 0 42px 0;
    background-color: $colorBgBlockSecondary;


    &_firstLine {
        .sectionInner {

            display        : flex;
            align-items    : center;
            justify-content: space-between;
            padding        : 12px 0;
        }

        &_links {
            @include RelewayMedium(14);
            color      : $colorTextPrimary;
            line-height: 18px;

            &_item {
                color : $colorTextPrimary;
                margin: 0 40px 0 0;

                &:last-child {
                    margin: 0;
                }
            }
        }

        &_feedback {
            display        : flex;
            align-items    : center;
            justify-content: space-between;

            &_item {
                color: $colorTextPrimary;
                @include RelewayMedium(14);
                line-height: 18px;

                margin: 0 40px 0 0;

                &:last-child {
                    margin: 0;
                }

            }

            .email {
                display        : flex;
                align-items    : center;
                justify-content: center;

                &::before {
                    content   : '';
                    display   : block;
                    width     : 18px;
                    height    : 18px;
                    background: transparent url(../img/mail.svg) 50% 50%/cover;
                    margin    : 0 4px 0 0;
                }
            }

            .phone {
                text-decoration: underline;
            }
        }
    }

    &_secondLine {
        position        : relative;
        background-color: $colorWhite;

        .sectionInner {
            display        : flex;
            align-items    : center;
            justify-content: flex-start;
            padding        : 24px 0;
        }

        &_logo {
            margin: 0 32px 0 0;
        }

        &_callback {
            display        : flex;
            flex-direction : column;
            align-items    : end;
            justify-content: space-between;
            @include RelewayRegular(14);
            line-height: 18px;
            margin     : 0 80px 0 0;

            &_text {
                margin: 0 0 4px 0;
                color : $colorTextThird;
            }

            &_phone {
                display        : flex;
                align-items    : center;
                justify-content: space-between;
                @include RelewayRegular(16);
                line-height: 20px;
                color      : $colorTextPrimary;

                span {
                    display   : inline-block;
                    width     : 20px;
                    height    : 20px;
                    background: transparent url(../img/phone.svg) 50% 50%/contain;
                    margin    : 0 8px 0 0;
                }

                .phoneText {
                    color          : $colorTextPrimary;
                    text-decoration: none;
                }
            }
        }

        &_searchField {
            width          : 360px;
            height         : 44px;
            display        : flex;
            align-items    : center;
            justify-content: flex-start;
            border-radius  : 8px;
            border         : 1px solid $colorBgBlock;
            margin         : 0 80px 0 0;


            &_icon {
                background: transparent;
                outline   : none;
                border    : none;
                margin    : 0 8px 0 0;
            }

            &_field {
                flex-grow       : 1 !important;
                outline         : none !important;
                border          : none !important;
                background-color: transparent !important;

                &::placeholder {
                    color: $colorTextThird
                }
            }
        }

        &_btns {
            flex: 1;

            &_item {
                border : none;
                outline: none;
                width  : 20px;
                height : 20px;
                margin : 0 24px 0 0;

                &:last-child {
                    margin: 0;
                }
            }

            .favorite {
                background     : transparent url(../img/favorite.svg) no-repeat 50% 50%;
                background-size: cover;
            }

            .compare {
                background     : transparent url(../img/compare.svg) no-repeat 50% 50%;
                background-size: cover;
            }

            .cart {
                background     : transparent url(../img/cart.svg) no-repeat 50% 50%;
                background-size: cover;
            }
        }

        &_personal {
            width           : 130px;
            height          : 48px;
            border-radius   : 8px;
            background-color: $colorTextPrimary;
            color           : $colorTextWhite;
            display         : flex;
            align-items     : center;
            justify-content : center;
            @include RelewayMedium(14);
            line-height: 18px;
        }


    }
}