.footer {

    @include RelewayRegular(12);
    border       : none;
    border-bottom: 1px solid $colorTextPrimary;

    &_firstLine {
        color : $colorTextSecondary;
        margin: 0 0 64px 0;

        .sectionInner {
            display        : flex;
            flex-direction : row;
            align-items    : flex-start;
            justify-content: space-between;

            span {
                display    : flex;
                align-items: flex-start;
                flex-grow  : 1;
                justify-content: space-between;
            }
        }

        &_logo {}

        &_user {
            width          : 140px;
            display        : flex;
            align-items    : flex-start;
            justify-content: flex-start;
            flex-direction : column;

            &_title {
                color: $colorTextPrimary;

                @include RelewayMedium(16);
                margin: 0 0 24px 0;
            }

            &_item {
                @include RelewayMedium(14);
                color : $colorTextSecondary;
                margin: 0 0 16px 0;

                &:last-child {
                    margin: 0;
                }
            }
        }

        &_catalog {
            width          : 320px;
            display        : flex;
            align-items    : flex-start;
            justify-content: flex-start;
            flex-direction : column;

            &_title {
                color: $colorTextPrimary;

                @include RelewayMedium(16);
                padding: 0 0 24px 0;
            }

            &_item {
                display        : flex;
                align-items    : center;
                justify-content: center;
                @include RelewayMedium(14);
                color : $colorTextSecondary;
                margin: 0 0 16px 0;

                &:last-child {
                    margin: 0;
                }

                &::before {
                    display      : block;
                    content      : '';
                    margin       : 0 8px 0 0;
                    width        : 18px;
                    height       : 18px;
                    border-radius: 4px;
                    background   : #F7F7FC url(../img/chevron.svg) no-repeat 50% 50%;
                    color        : #000;

                }
            }
        }

        &_about {
            width          : 350px;
            display        : flex;
            align-items    : flex-start;
            justify-content: flex-start;
            flex-direction : column;

            &_title {
                color: $colorTextPrimary;

                @include RelewayMedium(16);
                padding: 0 0 24px 0;
            }

            &_items {
                display        : flex;
                align-items    : flex-start;
                justify-content: flex-start;
                flex-direction : column;
                width          : 100%;
                height         : 170px;
                flex-wrap      : wrap;
            }

            &_item {
                @include RelewayMedium(14);
                color : $colorTextSecondary;
                margin: 0 0 16px 0;

                &:last-child {
                    margin: 0;
                }
            }
        }

        &_info {
            display        : flex;
            flex-direction : column;
            align-items    : flex-end;
            justify-content: center;
            min-width      : 225px;

            &_phone {
                @include RelewaySemiBold(24);
                color : $colorTextPrimary;
                margin: 0 0 8px 0;
            }

            &_social {

                display        : flex;
                align-items    : center;
                justify-content: flex-end;
                margin         : 0 0 8px 0;

                &_item {
                    display         : flex;
                    align-items     : center;
                    justify-content : center;
                    width           : 24px;
                    height          : 24px;
                    background-color: $colorTextPrimary;
                    border-radius   : 50%;
                    margin          : 0 8px 0 0;

                    &:last-child {
                        margin: 0;
                    }

                    &_img {
                        width: 11px;
                    }
                }
            }

            &_workTIme {
                color : $colorTextSecondary;
                margin: 0 0 8px 0;

                &_text {
                    @include RelewayMedium(12);
                    line-height: 16px;

                }

                &_time {
                    @include RelewayRegular(12);
                    line-height: 16px;
                }
            }

            &_email {
                color: $colorTextSecondary;
                @include RelewayRegular(12);
                line-height: 16px;
                margin     : 0 0 8px 0;

                &::after {
                    display   : inline-block;
                    content   : '';
                    background: url(../img/mail.svg);
                    width     : 16px;
                    height    : 16px;
                    margin    : 0 0 0 8px;
                }
            }

            &_address {
                color: $colorTextSecondary;
                @include RelewayRegular(12);
                line-height: 16px;
                margin     : 0 0 20px 0;

                &::after {
                    display   : inline-block;
                    content   : '';
                    background: url(../img/map.svg);
                    width     : 16px;
                    height    : 16px;
                    margin    : 0 0 0 8px;
                }
            }

            &_contactUs {
                background: none;
                outline   : none;
                border    : none;
                color     : $colorPrimary;
                @include RelewayMedium(16);
                line-height: 24px;
            }
        }
    }

    &_secondLine {
        padding         : 21px 0;
        color           : $colorTextWhite;
        background-color: $colorTextPrimary;

        .sectionInner {
            display        : flex;
            align-items    : center;
            justify-content: flex-start;
        }

        &_copyright {
            width      : 770px;
            padding    : 0 230px 0 0;
            flex       : 1;
            line-height: 18px;
        }

        &_link {
            color          : $colorTextWhite;
            text-decoration: underline;
            margin         : 0 24px 0 0;

            &:last-child {
                margin: 0;
            }
        }
    }
}