.sectionReviews {
    background-color: $colorWhite;
    background      : linear-gradient(180deg, $colorWhite 0%, $colorBgBlockSecondary 100%);
    border-radius   : 32px 32px 0 0;
    padding         : 64px 0;

    .sectionInner {}

    &_line {
        display        : flex;
        align-items    : center;
        justify-content: space-between;
        margin         : 0 0 40px 0;
        border         : none;
        border-bottom  : 1px solid $colorBgBlockSecondary;

        span {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width  : 100%;

        }

        &_title {
            font-size  : 32px;
            line-height: 40px;
        }

        &_rating {
            display        : flex;
            flex-direction : row;
            align-items    : center;
            justify-content: flex-start;

            .stars {
                display        : flex;
                flex-direction : row;
                align-items    : center;
                justify-content: flex-start;
                flex-wrap      : nowrap;
                flex-shrink    : 0;
                margin         : 0 25px 0 0;
            }

            .rating {
                @include RelewayMedium(12);
                line-height: 16px;
                margin     : 0 8px 0 0;
            }
        }

        &_btn {
            display        : flex;
            align-items    : center;
            justify-content: center;
        }
    }

    &_reviews {
        border-radius   : 16px;
        background-color: $colorWhite;
        box-shadow      : 0 34px 40px 1px rgba(0, 0, 0, 0.2);
        padding         : 0 24px;

        &_item {
            display        : flex;
            flex-direction : column;
            align-items    : flex-start;
            justify-content: flex-start;
            border         : none;
            border-bottom  : 1px solid $colorBgBlockSecondary;
            padding        : 24px 0;

            &_line {
                display        : flex;
                flex-direction : row;
                align-items    : center;
                justify-content: space-between;
                width          : 100%;
                margin         : 0 0 24px 0;

                &_title {
                    @include RelewayMedium(20);
                    line-height: 24px;
                }

                &_rating {
                    display        : flex;
                    flex-direction : row;
                    align-items    : center;
                    justify-content: flex-start;

                    .stars {
                        display        : flex;
                        flex-direction : row;
                        align-items    : center;
                        justify-content: flex-start;
                        flex-wrap      : nowrap;
                        flex-shrink    : 0;
                        margin         : 0 25px 0 0;
                    }
                }
            }

            &_text {
                @include RelewayRegular(16);
                line-height: 24px;
                width      : 480px;
                margin     : 0 0 24px 0;
                color      : $colorTextPrimary;
            }

            &_dateAndRate {
                margin         : 0 0 24px 0;
                display        : flex;
                flex-direction : row;
                align-items    : center;
                justify-content: space-between;
                width          : 100%;

                &_date {
                    color: $colorTextThird;
                    @include RelewayRegular(14);
                    line-height: 18px;

                }

                &_rate {
                    display        : flex;
                    align-items    : center;
                    justify-content: space-between;

                    .like {
                        display        : flex;
                        flex-direction : row;
                        align-items    : center;
                        justify-content: space-between;
                        margin         : 0 12px 0 0;

                        .count {
                            display        : flex;
                            align-items    : center;
                            justify-content: center;
                            @include RelewayMedium(12);
                            line-height: 16px;
                            color      : $colorTextThird;
                            margin     : 0 4px 0 0;
                        }

                        img {}
                    }



                    .dislike {
                        display        : flex;
                        flex-direction : row;
                        align-items    : center;
                        justify-content: space-between;

                        .count {
                            display        : flex;
                            align-items    : center;
                            justify-content: center;
                            @include RelewayMedium(12);
                            line-height: 16px;
                            color      : $colorTextThird;
                            margin     : 0 4px 0 0;
                        }

                        img {}
                    }
                }
            }

            &_btn {
                align-self: flex-end;
                color     : $colorPrimary;
                @include RelewayRegular(14);
                line-height: 18px;
                border     : none;
                outline    : none;
                background : none;
                cursor     : pointer;
            }
        }
    }
}