.sectionItem {
    margin    : 0 0 40px 0;
    min-height: 350px;

    .sectionInner {
        display        : flex;
        flex-direction : row;
        align-items    : flex-start;
        justify-content: flex-start;
        height         : 100%;
    }

    &_gallery {
        margin         : 0 24px 0 0;
        width          : 400px;
        height         : 350px;
        display        : flex;
        align-items    : flex-start;
        justify-content: space-between;

        .swiper-slide {
            text-align     : center;
            font-size      : 18px;
            background     : #fff;
            display        : flex;
            justify-content: center;
            align-items    : center;
        }

        .swiper-slide img {
            display   : block;
            width     : 100%;
            height    : 100%;
            object-fit: cover;
        }

        body {
            background: #000;
            color     : #000;
        }

        .swiper {
            width       : 100%;
            height      : 100%;
            margin-left : auto;
            margin-right: auto;
        }

        .swiper-slide {
            background-size    : cover;
            background-position: center;
        }

        .mainProductSlider {
            width: 80%;
            width: 100%;
        }

        .thumbSlider {
            width     : 20%;
            height    : 100%;
            margin    : 0 8px 0 0;
            box-sizing: border-box;
            padding   : 10px 0;
            position  : relative;

            .mainProductSlider-button-next {
                position  : absolute;
                bottom    : 0;
                left      : 0;
                top       : auto;
                right     : auto;
                width     : 100%;
                height    : 30px;
                z-index   : 999;
                background: #ffffff4b url(../img/slider_btn_inactive.svg) 50% 50%/ 75% 100% no-repeat;

                &:hover {
                    background: #ffffffb4 url(../img/slider_btn_inactive.svg) 50% 50%/ 75% 100% no-repeat;
                }

                &.swiper-button-disabled {
                    opacity: 0.5;

                    &:hover {
                        background: #ffffff4b url(../img/slider_btn_inactive.svg) 50% 50%/ 75% 100% no-repeat;
                    }
                }
            }

            .mainProductSlider-button-prev {
                position  : absolute;
                bottom    : auto;
                left      : 0;
                top       : 0;
                right     : auto;
                width     : 100%;
                height    : 30px;
                z-index   : 999;
                transform : rotate(180deg);
                background: #ffffff4b url(../img/slider_btn_inactive.svg) 50% 50%/ 75% 100% no-repeat;

                &:hover {
                    background: #ffffffb4 url(../img/slider_btn_inactive.svg) 50% 50%/ 75% 100% no-repeat;
                }

                &.swiper-button-disabled {
                    opacity: 0.5;

                    &:hover {
                        background: #ffffff4b url(../img/slider_btn_inactive.svg) 50% 50%/ 75% 100% no-repeat;
                    }
                }
            }
        }

        .thumbSlider .swiper-slide {
            width        : 100%;
            height       : 25%;
            opacity      : 0.4;
            border-radius: 12px;

            img {
                border-radius: 12px;

            }
        }

        .thumbSlider .swiper-slide-thumb-active {
            opacity: 1;
        }

        .swiper-slide img {
            display   : block;
            width     : 100%;
            height    : 100%;
            object-fit: cover;
        }


    }

    &_characteristic {
        margin         : 0 24px 0 0;
        height         : 100%;
        display        : flex;
        flex-direction : column;
        align-items    : flex-start;
        justify-content: flex-start;

        &_title {
            @include RelewaySemiBold(16);
            line-height: 24px;
            color      : $colorTextPrimary;
            margin     : 0 0 24px 0;
        }

        &_charTitle {
            @include RelewaySemiBold(16);
            line-height: 24px;
            color      : $colorTextPrimary;
            margin     : 0 0 12px 0;
        }

        &_info {
            display        : flex;
            align-items    : flex-start;
            justify-content: flex-start;
            flex-grow      : 1;
            margin         : 0 0 24px 0;

            &_title {
                @include RelewayMedium(14);
                line-height: 18px;
                color      : $colorTextSecondary;
                margin     : 0 16px 0 0;

                &::after {
                    content: ':';
                }
            }

            &_text {

                color: $colorTextPrimary;
                @include RelewaySemiBold(14);
                line-height: 18px;
            }
        }

        &_links {
            display        : flex;
            flex-direction : row;
            align-items    : center;
            justify-content: flex-start;

            &_item {
                margin         : 0 24px 0 0;
                color          : $colorBlue;
                text-decoration: none;
                @include RelewayMedium(12);
                line-height: 16px;

                &.addReview {
                    color : $colorTextSecondary;
                    margin: 0;
                }
            }
        }

        &_priceAndActions {
            background-color: $colorWhite;
            padding         : 16px;
            width           : 290px;
            border-radius   : 12px;

            &_rating {
                display        : flex;
                flex-direction : row;
                align-items    : center;
                justify-content: flex-start;
                margin         : 0 0 16px 0 0;

                .stars {
                    display        : flex;
                    flex-direction : row;
                    align-items    : center;
                    justify-content: flex-start;
                    flex-wrap      : nowrap;
                    flex-shrink    : 0;
                }

                .rating {
                    @include RelewayMedium(12);
                    line-height: 16px;
                    flex-grow  : 1;
                }

                .addVaforite {
                    width     : 20px;
                    height    : 20px;
                    outline   : none;
                    border    : none;
                    background: transparent url(../img/favorite_gray.svg) 50% 50%/cover no-repeat;
                    margin    : 0 12px 0 0;
                }

                .compare {
                    width     : 20px;
                    height    : 20px;
                    outline   : none;
                    border    : none;
                    background: transparent url(../img/compare_gray.svg) 50% 50%/cover no-repeat;
                }
            }

            &_price {
                display        : flex;
                flex-direction : column;
                align-items    : flex-start;
                justify-content: flex-start;
                position       : relative;
                width          : 160px;

                .oldPrice {
                    @include RelewayMedium(16);
                    line-height    : 24px;
                    margin         : 0 0 5px 0;
                    color          : $colorTextThird;
                    text-decoration: line-through;

                    &::after {
                        content: ' ₽';
                    }
                }

                .discont {
                    position        : absolute;
                    top             : 0;
                    right           : 0;
                    z-index         : 2;
                    padding         : 4px 16px;
                    background-color: $colorGreen;
                    @include RelewayBold(18);
                    line-height: 22px;

                    span {
                        &::after {
                            content: '%';
                        }

                        &::before {
                            content: '-';
                        }
                    }
                }

                .newPrice {
                    @include RelewaySemiBold(32);
                    line-height: 40px;
                    margin     : 0 0 12px 0;

                    &::after {
                        content: ' ₽';
                    }
                }

                .inStock {
                    @include RelewayRegular(14);
                    line-height: 18px;
                    color      : $colorTextPrimary;
                    margin     : 0 0 17px 0;
                }
            }

            &_counterAndBtn {
                .line {
                    display        : flex;
                    flex-direction : row;
                    align-items    : center;
                    justify-content: space-between;
                    width          : 100%;
                    margin         : 0 0 17px 0;

                }

                .quantity-block {
                    display        : flex;
                    flex-direction : row;
                    align-items    : center;
                    border         : 1px solid $colorBgBlockSecondary;
                    border-radius  : 8px;
                    justify-content: space-between;

                    input[type=number]::-webkit-inner-spin-button,
                    input[type=number]::-webkit-outer-spin-button {
                        -webkit-appearance: none;
                        -moz-appearance   : none;
                        appearance        : none;
                        margin            : 0;
                    }

                    /* Для Firefox */
                    input[type=number] {
                        -moz-appearance: textfield;
                    }

                    .quantity-arrow-minus {
                        width  : toRem(25);
                        height : toRem(40);
                        border : none;
                        outline: none;
                        color  : $colorTextDark;
                        @include RelewaySemiBold(16);
                        line-height: 24px;
                        background : none;
                    }

                    .quantity-arrow-plus {
                        width  : toRem(25);
                        height : toRem(40);
                        border : none;
                        outline: none;
                        color  : $colorTextDark;
                        @include RelewaySemiBold(16);
                        line-height: 24px;
                        background : none;
                    }

                    .quantity-num {
                        width     : toRem(45);
                        height    : toRem(40);
                        outline   : none;
                        text-align: center;
                        color     : $colorTextDark;
                        @include RelewaySemiBold(16);
                        line-height: 24px;
                        border     : none;

                    }
                }

                &_btn {
                    display        : flex;
                    align-items    : center;
                    justify-content: center;
                    @include RelewaySemiBold(16);
                    line-height: 24px;
                    padding    : 12px 24px;
                }

                &_buyOneClick {
                    background-color: $colorWhite;
                    color           : $colorPrimary;
                    border          : 1px solid $colorPrimary;
                    display         : flex;
                    justify-content : center;
                    align-items     : center;
                    @include RelewaySemiBold(16);
                    line-height: 24px;
                    padding    : 12px 24px;
                    margin     : 0 0 34px 0;

                }


            }

            &_links {
                display        : flex;
                flex-direction : row;
                align-items    : center;
                justify-content: flex-start;
                border         : none;
                border-top     : 1px solid $colorBgBlockSecondary;
                padding        : 16px 0 0 0;

                &_item {
                    display        : flex;
                    align-items    : center;
                    justify-content: flex-start;
                    @include RelewayRegular(12);
                    line-height    : 16px;
                    display        : flex;
                    align-items    : center;
                    justify-content: center;
                    margin         : 0 8px 0 0;
                    color          : $colorTextThird;

                    &::before {
                        display   : block;
                        content   : '';
                        width     : 4px;
                        height    : 4px;
                        background: transparent url(../img/dot.svg) 50% 50%/cover no-repeat;
                        margin    : 0 8px 0 0;
                    }

                    &.delivery {}

                    &.pay {}

                    &.share {
                        flex-grow      : 1;
                        margin         : 0;
                        align-self     : flex-end;
                        justify-content: flex-end;

                        &::before {
                            display   : block;
                            content   : '';
                            width     : 18px;
                            height    : 18px;
                            background: transparent url(../img/share.svg) 50% 50%/cover no-repeat;
                            margin    : 0 8px 0 0;
                        }
                    }
                }
            }
        }
    }
}