.sectionNews {
    padding         : 64px 0 64px 0;
    background-color: $colorBgBlockSecondary;

    .sectionInner {}

    &_line {
        display        : flex;
        flex-direction : row;
        justify-content: space-between;

        &_title {}

        &_sliderBtns {
            display: flex;

            .swiper-button-disabled {
                opacity: 0.5;
                cursor : auto;

                &:hover {
                    background-color: transparent;

                    span {
                        background-image: url(../img/chevron_inactive.svg) !important;
                    }

                }
            }

            &_btn_prev {
                width        : 55px;
                height       : 55px;
                flex-shrink  : 0;
                border-radius: 12px;
                transition   : 0.5s;
                cursor       : pointer;

                &:hover {
                    background-color: $colorTextThird;

                    span {
                        background-image: url(../img/chevron_active.svg) !important;
                    }

                }

            }

            .prev {
                transform: rotate(180deg);

                span {
                    display   : block;
                    width     : 100%;
                    height    : 100%;
                    background: transparent url(../img/chevron_inactive.svg) 50% 50% no-repeat;
                }
            }

            &_btn_next {
                flex-shrink  : 0;
                width        : 55px;
                height       : 55px;
                border-radius: 12px;
                transition   : 0.5s;
                margin       : 0 0 0 12px;
                cursor       : pointer;

                &:hover {
                    background-color: $colorTextThird;

                    span {
                        background-image: url(../img/chevron_active.svg) !important;
                    }
                }
            }

            .next {
                span {
                    display   : block;
                    width     : 100%;
                    height    : 100%;
                    background: transparent url(../img/chevron_inactive.svg) 50% 50% no-repeat;
                }

            }
        }

        &_slider {
            .swiper-wrapper {
                padding: 0 0 50px 0;
            }

            &_item {
                width           : 430px;
                height          : 530px;
                display         : flex;
                flex-direction  : column;
                align-items     : center;
                justify-content : space-between;
                border-radius   : 16px;
                background-color: $colorWhite;
                position        : relative;

                &_img {
                    position     : absolute;
                    top          : 0;
                    left         : 0;
                    width        : 100%;
                    height       : 245px;
                    z-index      : 1;
                    border-radius: 16px 16px 0 0;
                }

                &_info {
                    background-color: $colorWhite;
                    height          : 100%;
                    position        : relative;
                    z-index         : 3;
                    margin          : 230px 0 0 0;
                    border-radius   : 16px;
                    padding         : 24px;
                    display         : flex;
                    flex-direction  : column;
                    justify-content : space-between;

                    &_category {
                        display: block;
                        margin : 0 0 24px 0;
                        @include RelewayMedium(16);
                        line-height: 24px;
                        color      : $colorTextSecondary;
                    }

                    &_title {
                        overflow          : hidden;
                        display           : -webkit-box;
                        -webkit-line-clamp: 3;
                        -webkit-box-orient: vertical;
                        line-height       : 1.3em;
                        height            : 3.9em;
                        @include RelewaySemiBold(26);
                        line-height: 32px;

                    }

                    &_date {
                        @include RelewaySemiBold(16);
                        color          : $colorTextThird;
                        line-height    : 20px;
                        flex-grow      : 1;
                        display        : flex;
                        align-items    : flex-end;
                        justify-content: flex-start;
                    }
                }
            }
        }
    }
}