@function toRem($value) {
    // $remValue   : calc($value / 16) + rem;
    $remValue: $value + px;
    @return $remValue;
}

@mixin RelewayRegular($size_px) {
    font-family: 'Releway', sans-serif;
    font-weight: 400;
    font-size  : toRem($size_px);
    font-style : normal;
}

@mixin RelewayMedium($size_px) {
    font-family: 'Releway', sans-serif;
    font-weight: 500;
    font-size  : toRem($size_px);
    font-style : normal;
}

@mixin RelewaySemiBold($size_px) {
    font-family: 'Releway', sans-serif;
    font-weight: 600;
    font-size  : toRem($size_px);
    font-style : normal;
}

@mixin RelewayBold($size_px) {
    font-family: 'Releway', sans-serif;
