.seoText {
    background-color: $colorWhite;
    padding         : 0 0 40px 0;

    .sectionInner {
        background-color: $colorBgBlockSecondary;
        padding         : 48px 40px;
        border-radius   : 32px;
    }

    .seoText_title {}

    .seoText_paragraph {
        color: $colorTextSecondary;
        @include RelewayMedium(16);
        line-height: 24px;
        margin     : 0 0 16px 0;

        &:last-child {
            margin: 0;

        }
    }
}