.sectionOffer {
    padding         : 64px 0 64px 0;
    background-color: $colorWhite;
    border-radius   : 48px 48px 0 0;

    .sectionInner {
        display        : flex;
        flex-direction : column;
        align-items    : start;
        justify-content: start;

    }

    &_title {}

    &_cardsWrapper {
        display        : flex;
        align-items    : center;
        justify-content: space-between;
        flex-direction : row;
        width          : 100%;
        margin         : 0 0 64px 0;

        &_card {
            display         : flex;
            flex-direction  : column;
            align-items     : flex-start;
            justify-content : space-between;
            width           : 330px;
            height          : 450px;
            background-color: $colorBgBlockSecondary;
            border-radius   : 16px;
            padding         : 32px 0 32px 16px;

            &_title {
                @include RelewaySemiBold(24);
                line-height: 32px;
                color      : $colorTextPrimary;
            }

            &_img {
                align-self           : flex-end;
                width                : 250px;
                height               : 220px;
                background-repeat    : no-repeat;
                background-size      : contain;
                background-blend-mode: darken;
                background-color     : $colorBgBlockSecondary;
            }
        }
    }

    &_text {
        color: $colorTextSecondary;
        @include RelewaySemiBold(32);
        line-height: 40px;
    }
}