* {
    margin    : 0;
    padding   : 0;
    box-sizing: border-box;
}

html {
    background-color: $colorBgBlockSecondary;
}

.sectionInner {
    width : 1360px;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

.btn {
    padding         : 16px 24px;
    background-color: $colorPrimary;
    outline         : none;
    border          : none;
    border-radius   : 8px;
    color           : $colorWhite;
    white-space     : nowrap;
}

.title {
    @include RelewaySemiBold(48);
    line-height: 56px;
    color      : #000;
    margin     : 0 0 40px 0;
}

.breadcrumbs {
    .sectionInner {
        display        : flex;
        flex-direction : row;
        align-items    : center;
        justify-content: flex-start;
    }

    margin : 0 0 40px 0;

    &Main {
        display   : block;
        width     : 18px;
        height    : 18px;
        background: transparent url(../img/home.svg) 50% 50%/cover no-repeat;

    }

    &Element {
        display        : flex;
        align-items    : center;
        justify-content: flex-start;
        color          : $colorTextThird;
        @include RelewayRegular(12);
        line-height: 16px;

        &::before {
            content   : '';
            display   : block;
            width     : 4px;
            height    : 4px;
            background: transparent url(../img/dot.svg) 50% 50%/cover no-repeat;
            margin    : 0 8px;
        }
    }
}

.tooltip {
    position: relative;
    display : inline-block;
}

/* Tooltip text */
.tooltip .tooltiptext {
    visibility      : hidden;
    width           : 120px;
    background-color: black;
    color           : #fff;
    text-align      : center;
    padding         : 5px 0;
    border-radius   : 6px;

    /* Position the tooltip text - see examples below! */
    position: absolute;
    z-index : 1;
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext {
    visibility: visible;
}