.sectionPopular {
    background-color: $colorWhite;
    padding         : 0 0 64px 0;

    .sectionInner {}

    &_line {
        display        : flex;
        flex-direction : row;
        justify-content: space-between;

        &_title {}

        &_sliderBtns {
            display: flex;

            .swiper-button-disabled {
                opacity: 0.5;
                cursor : auto;

                &:hover {
                    background-color: transparent;

                    span {
                        background-image: url(../img/chevron_inactive.svg) !important;
                    }

                }
            }

            &_btn_prev {
                width        : 55px;
                height       : 55px;
                flex-shrink  : 0;
                border-radius: 12px;
                transition   : 0.5s;
                cursor       : pointer;

                &:hover {
                    background-color: $colorTextThird;

                    span {
                        background-image: url(../img/chevron_active.svg) !important;
                    }

                }

            }

            .prev {
                transform: rotate(180deg);

                span {
                    display   : block;
                    width     : 100%;
                    height    : 100%;
                    background: transparent url(../img/chevron_inactive.svg) 50% 50% no-repeat;
                }
            }

            &_btn_next {
                flex-shrink  : 0;
                width        : 55px;
                height       : 55px;
                border-radius: 12px;
                transition   : 0.5s;
                margin       : 0 0 0 12px;
                cursor       : pointer;

                &:hover {
                    background-color: $colorTextThird;

                    span {
                        background-image: url(../img/chevron_active.svg) !important;
                    }
                }
            }

            .next {
                span {
                    display   : block;
                    width     : 100%;
                    height    : 100%;
                    background: transparent url(../img/chevron_inactive.svg) 50% 50% no-repeat;
                }

            }
        }

        &_slider {
            display        : flex;
            flex-direction : row;
            justify-content: space-between;
            width          : 100%;

            .swiper-wrapper {
                padding: 0 0 50px 0;
            }

            &_item {
                background-color: $colorBgBlockSecondary;
                width           : 550px;
                height          : 320px;
                border-radius   : 16px;
                display         : flex;
                flex-direction  : row;
                padding         : 24px;

                &_info {
                    display        : flex;
                    flex-direction : column;
                    align-items    : flex-start;
                    justify-content: space-between;
                    flex-grow      : 1
                }

                &_category {
                    @include RelewayBold(16);
                    line-height: 24px;
                    color      : $colorTextFourth;
                }

                &_title {
                    flex-grow: 1;
                    @include RelewayBold(32);
                    line-height: 40px;
                    color      : $colorTextDark;
                }

                &_price {
                    @include RelewaySemiBold(19);
                    line-height: 23px;
                    color      : $colorTextDark;
                }

                &_img {
                    width                : 200px;
                    height               : 280px;
                    background-color     : $colorBgBlockSecondary;
                    background-blend-mode: darken;
                }
            }
        }
    }
}