.sectionRecentlyViewed,
.sectionAccessories {
    padding         : 64px 0 64px 0;
    background-color: $colorBgBlockSecondary;

    .sectionInner {}

    &_line {
        display        : flex;
        flex-direction : row;
        justify-content: space-between;

        &_title {}

        &_sliderBtns {
            display: flex;

            .swiper-button-disabled {
                opacity: 0.5;
                cursor : auto;

                &:hover {
                    background-color: transparent;

                    span {
                        background-image: url(../img/chevron_inactive.svg) !important;
                    }

                }
            }

            &_btn_prev {
                width        : 55px;
                height       : 55px;
                flex-shrink  : 0;
                border-radius: 12px;
                transition   : 0.5s;
                cursor       : pointer;

                &:hover {
                    background-color: $colorTextThird;

                    span {
                        background-image: url(../img/chevron_active.svg) !important;
                    }

                }

            }

            .prev {
                transform: rotate(180deg);

                span {
                    display   : block;
                    width     : 100%;
                    height    : 100%;
                    background: transparent url(../img/chevron_inactive.svg) 50% 50% no-repeat;
                }
            }

            &_btn_next {
                flex-shrink  : 0;
                width        : 55px;
                height       : 55px;
                border-radius: 12px;
                transition   : 0.5s;
                margin       : 0 0 0 12px;
                cursor       : pointer;

                &:hover {
                    background-color: $colorTextThird;

                    span {
                        background-image: url(../img/chevron_active.svg) !important;
                    }
                }
            }

            .next {
                span {
                    display   : block;
                    width     : 100%;
                    height    : 100%;
                    background: transparent url(../img/chevron_inactive.svg) 50% 50% no-repeat;
                }

            }
        }

        &_slider {
            .swiper-wrapper {
                padding: 0 0 50px 0;
            }

            &_item {
                width           : 430px;
                height          : 600px;
                display         : flex;
                flex-direction  : column;
                align-items     : center;
                justify-content : space-between;
                border-radius   : 16px;
                background-color: $colorWhite;
                position        : relative;

                &_img {
                    position     : absolute;
                    top          : 0;
                    left         : 0;
                    width        : 100%;
                    height       : 245px;
                    z-index      : 1;
                    border-radius: 16px 16px 0 0;
                }

                &_info {
                    background-color: $colorWhite;
                    height          : 100%;
                    position        : relative;
                    z-index         : 3;
                    margin          : 230px 0 0 0;
                    border-radius   : 16px;
                    padding         : 24px;
                    display         : flex;
                    flex-direction  : column;
                    justify-content : space-between;


                    &_rating {
                        display        : flex;
                        flex-direction : row;
                        align-items    : center;
                        justify-content: flex-start;
                        margin         : 0 0 24px 0;

                        .stars {
                            display        : flex;
                            flex-direction : row;
                            align-items    : center;
                            justify-content: flex-start;
                            flex-wrap      : nowrap;
                            flex-shrink    : 0;
                        }

                        .rating {
                            @include RelewayMedium(12);
                            line-height: 16px;
                            margin     : 0 8px 0 0;
                        }


                    }

                    &_price {
                        display        : flex;
                        flex-direction : column;
                        align-items    : flex-start;
                        justify-content: flex-start;
                        position       : relative;
                        margin         : 0 0 12px 0;
                        width          : 156px;

                        .oldPrice {
                            @include RelewayMedium(16);
                            line-height    : 24px;
                            margin         : 0 0 5px 0;
                            color          : $colorTextThird;
                            text-decoration: line-through;

                            &::after {
                                content: ' ₽';
                            }
                        }

                        .discont {
                            position        : absolute;
                            top             : 0;
                            right           : 0;
                            z-index         : 2;
                            padding         : 4px 16px;
                            background-color: $colorGreen;
                            @include RelewayBold(18);
                            line-height: 22px;

                            span {
                                &::after {
                                    content: '%';
                                }

                                &::before {
                                    content: '-';
                                }
                            }
                        }

                        .newPrice {
                            @include RelewaySemiBold(32);
                            line-height: 40px;

                            &::after {
                                content: ' ₽';
                            }
                        }
                    }

                    .inStock {
                        @include RelewayRegular(14);
                        line-height: 18px;
                        color      : $colorTextPrimary;
                        margin     : 0 0 24px 0;
                    }

                    &_btns {
                        display        : flex;
                        flex-direction : row;
                        align-items    : center;
                        justify-content: space-between;
                        flex-grow      : 1;

                        &_item {
                            margin         : 0 24px 0 0;
                            width          : 100%;
                            display        : flex;
                            align-items    : center;
                            justify-content: center;
                        }

                        .actions {
                            display        : flex;
                            flex-direction : column;
                            align-items    : flex-start;
                            justify-content: space-between;
                            height         : 100%;

                            .addVaforite {
                                display        : flex;
                                align-items    : center;
                                justify-content: center;
                                flex-direction : row;
                                outline        : none;
                                border         : none;
                                background     : none;
                                color          : $colorTextThird;
                                @include RelewayMedium(14);
                                line-height: 18px;

                                img {
                                    margin: 0 0 4px 0;
                                }
                            }

                            .compare {
                                display        : flex;
                                align-items    : center;
                                justify-content: center;
                                flex-direction : row;
                                outline        : none;
                                border         : none;
                                background     : none;
                                color          : $colorTextThird;
                                @include RelewayMedium(14);
                                line-height: 18px;

                                img {
                                    margin: 0 0 4px 0;
                                }
                            }
                        }
                    }


                    &_title {

                        @include RelewaySemiBold(26);
                        line-height: 32px;
                        margin     : 0 0 24px 0;

                    }

                    &_date {
                        @include RelewaySemiBold(16);
                        color          : $colorTextThird;
                        line-height    : 20px;
                        flex-grow      : 1;
                        display        : flex;
                        align-items    : flex-end;
                        justify-content: flex-start;
                    }
                }
            }
        }
    }
}