.sectionCharacteristic {
    &_content {
        display: none;

        &.active {
            display: inline;
        }
    }

    .sectionInner {
        background-color: $colorWhite;
        border-radius   : 32px;
        padding         : 40px;
    }

    &_btns {
        display           : flex;
        flex-direction    : row;
        align-items       : center;
        justify-content   : flex-start;
        margin            : 0 0 24px 0;
        overflow-x        : scroll;
        -ms-overflow-style: none;
        /* IE и Edge */
        scrollbar-width   : none;
        /* Firefox */

        &::-webkit-scrollbar {
            display: none;
        }




        &_btn {
            background-color: $colorBgBlockSecondary;
            color           : $colorTextPrimary;
            margin          : 0 12px 0 0;
            transition      : 0.5s;
            cursor          : pointer;

            &:last-child {
                margin: 0;
            }

            &:hover {
                background-color: $colorPrimary;
                color           : $colorTextWhite;
            }
        }

        .active {
            background-color: $colorPrimary;
            color           : $colorTextWhite;
        }
    }

    &_blockInfo {
        margin: 0 0 40px 0;

        &_title {
            @include RelewaySemiBold(24);
            line-height: 32px;
            margin     : 0 0 16px 0;
            color      : $colorTextPrimary;
        }

        &_info {
            @include RelewayMedium(16);
            line-height: 24px;
            color      : $colorTextPrimary;

        }

        &_allChar {
            table {
                width          : 100%;
                border-collapse: collapse;
                @include RelewayMedium(16);
                line-height: 24px;
                color      : $colorTextSecondary;
            }

            tr {}

            td {
                border       : none;
                padding      : 12px 0;
                border-bottom: 1px solid $colorBgBlockSecondary;
                width        : 50%;
            }

            .value {
                text-align: end
            }
        }

        &_howBuy {
            &_title {
                @include RelewaySemiBold(24);
                line-height: 32px;
                margin     : 0 0 16px 0;
                color      : $colorTextPrimary;
            }

            &_info {
                @include RelewayMedium(16);
                line-height: 24px;
                color      : $colorTextPrimary;

            }
        }

        &_docs {
            &_title {
                @include RelewaySemiBold(24);
                line-height: 32px;
                margin     : 0 0 16px 0;
                color      : $colorTextPrimary;
            }

            &_info {
                @include RelewayMedium(16);
                line-height: 24px;
                color      : $colorTextPrimary;
                margin     : 0 0 40px 0
            }

            &_list {
                display              : grid;
                grid-template-columns: repeat(4, 1fr);
                gap                  : 30px;

                .item {
                    display        : flex;
                    flex-direction : row;
                    align-items    : center;
                    justify-content: flex-start;
                    width          : 100%;
                    color          : $colorTextSecondary;
                    @include RelewayMedium(14);
                    line-height: 18px;

                    .icon {
                        width : 32px;
                        height: 32px;
                        margin: 0 16px 0 0;
                    }
                }
            }
        }

        &_gallery {
            &_title {
                @include RelewaySemiBold(24);
                line-height: 32px;
                margin     : 0 0 16px 0;
                color      : $colorTextPrimary;
            }

            &_info {
                @include RelewayMedium(16);
                line-height: 24px;
                color      : $colorTextPrimary;
                margin     : 0 0 40px 0
            }

            &_list {
                display              : grid;
                grid-template-columns: repeat(3, 1fr);
                gap                  : 30px;

                img {
                    width        : 100%;
                    height       : 100%;
                    border-radius: 12px;
                }
            }
        }
    }

    &_listInfo {
        &_title {
            @include RelewaySemiBold(24);
            line-height: 32px;
            margin     : 0 0 16px 0;
            color      : $colorTextPrimary;
        }

        &_list {
            list-style-position: inside;

            &_item {
                @include RelewayMedium(16);
                line-height: 24px;
                color      : $colorTextPrimary;
            }
        }
    }
}