.sectionCatalog {

    background-color: $colorWhite;
    border-radius   : 32px 32px 0 0;
    padding         : 0 0 40px 0;

    .sectionInner {}

    &_title {}

    &_catalog {
        width          : 100%;
        display        : flex;
        flex-direction : row;
        align-items    : flex-start;
        justify-content: space-between;

        &_filter {
            background-color: $colorBgBlockSecondary;
            border-radius   : 8px;
            padding         : 16px;
            width           : 315px;
            margin          : 0 32px 0 0;

            &_group {
                &_title {
                    color: $colorTextThird;
                    @include RelewayMedium(14);
                    line-height: 18px;
                    margin     : 0 0 16px 0;
                }

                label {
                    display        : flex;
                    align-items    : center;
                    justify-content: flex-start;
                    color          : $colorTextPrimary;
                    @include RelewayRegular(16);
                    line-height: 24px;
                    margin     : 0 0 12px 0;

                    &:before {
                        display   : block;
                        content   : "";
                        width     : 24px;
                        height    : 24px;
                        background: transparent url(../img/checkbox_inactive.svg) 50% 50%/cover no-repeat;
                        margin    : 0 8px 0 0;
                    }

                }

                input {
                    display: none;

                }

                input:checked+label:before {
                    background-image: url(../img/checkbox_active.svg);
                }
            }

            .resetBtn {
                width           : 100%;
                background-color: $colorWhite;
                border          : 1px solid $colorTextThird;
                outline         : none;
                color           : $colorTextDark;
                @include RelewaySemiBold(16);
                line-height    : 24px;
                display        : flex;
                align-items    : center;
                justify-content: center;

                .darkClose {
                    width     : 24px;
                    height    : 24px;
                    display   : block;
                    margin    : 0 0 0 12px;
                    background: transparent url(../img/blackClose.svg) 50% 50%/cover no-repeat;
                }
            }
        }

        &_list {
            flex-grow      : 1;
            display        : flex;
            flex-direction : column;
            align-items    : flex-start;
            justify-content: flex-start;

            &_tags {
                display        : flex;
                flex-direction : column;
                flex-wrap      : wrap;
                align-items    : center;
                justify-content: flex-start;
                margin         : 0 0 16px 0;

                &_item {
                    color           : $colorWhite;
                    background-color: $colorTextThird;
                    @include RelewayMedium(12);
                    line-height    : 16px;
                    border-radius  : 4px;
                    display        : flex;
                    align-items    : center;
                    justify-content: center;
                    padding        : 10px 12px;
                    margin         : 0 10px 10px 0;

                    &:last-child {
                        margin-right: 0;
                    }

                    &_close {
                        margin         : 0 4px 0 0;
                        display        : flex;
                        align-items    : center;
                        justify-content: center;
                    }

                    &_text {}
                }
            }

            &_fastFiler {
                display        : flex;
                align-items    : center;
                justify-content: flex-start;
                margin         : 0 0 24px 0;

                &_item {
                    color: $colorBlue;
                    @include RelewayMedium(12);
                    line-height: 16px;
                    margin     : 0 16px 0 0;
                    cursor     : pointer;

                    &:last-child {
                        margin: 0;
                    }

                    &.active {
                        color: $colorTextPrimary;
                    }
                }

            }

            &_itemList {
                &_item {
                    display        : flex;
                    flex-direction : row;
                    align-items    : flex-start;
                    justify-content: space-between;
                    padding        : 32px 0;
                    border         : none;
                    border-bottom  : 1px solid $colorBgBlockSecondary;

                    &:first-child {
                        padding-top: 0;
                    }

                    &_img {}

                    &_info {
                        display        : flex;
                        flex-direction : column;
                        align-items    : flex-start;
                        justify-content: flex-start;
                        margin         : 0 24px;

                        &_title {
                            @include RelewaySemiBold(16);
                            line-height: 24px;
                            color      : $colorTextPrimary;
                            margin     : 0 0 24px 0;
                        }

                        &_characteristic {
                            display        : flex;
                            align-items    : center;
                            justify-content: flex-start;

                            &_title {
                                @include RelewayMedium(14);
                                line-height: 18px;
                                color      : $colorTextSecondary;
                                margin     : 0 16px 0 0;

                                &::after {
                                    content: ':';
                                }
                            }

                            &_text {

                                color: $colorTextPrimary;
                                @include RelewaySemiBold(14);
                                line-height: 18px;
                            }
                        }
                    }

                    &_priceAndActions {
                        &_rating {
                            display        : flex;
                            flex-direction : row;
                            align-items    : center;
                            justify-content: flex-start;
                            margin         : 0 0 25px 0 0;

                            .stars {
                                display        : flex;
                                flex-direction : row;
                                align-items    : center;
                                justify-content: flex-start;
                                flex-wrap      : nowrap;
                                flex-shrink    : 0;
                            }

                            .rating {
                                @include RelewayMedium(12);
                                line-height: 16px;
                                margin     : 0 45px 0 0;
                            }

                            .addVaforite {
                                width     : 20px;
                                height    : 20px;
                                outline   : none;
                                border    : none;
                                background: transparent url(../img/favorite_gray.svg) 50% 50%/cover no-repeat;
                                margin    : 0 12px 0 0;
                            }

                            .compare {
                                width     : 20px;
                                height    : 20px;
                                outline   : none;
                                border    : none;
                                background: transparent url(../img/compare_gray.svg) 50% 50%/cover no-repeat;
                            }
                        }

                        &_price {
                            display        : flex;
                            flex-direction : column;
                            align-items    : flex-start;
                            justify-content: flex-start;
                            position       : relative;
                            margin         : 0 0 20px 0;

                            .oldPrice {
                                @include RelewayMedium(16);
                                line-height    : 24px;
                                margin         : 0 0 5px 0;
                                color          : $colorTextThird;
                                text-decoration: line-through;

                                &::after {
                                    content: ' ₽';
                                }
                            }

                            .discont {
                                position        : absolute;
                                top             : 0;
                                right           : 0;
                                z-index         : 2;
                                padding         : 4px 16px;
                                background-color: $colorGreen;
                                @include RelewayBold(18);
                                line-height: 22px;

                                span {
                                    &::after {
                                        content: '%';
                                    }

                                    &::before {
                                        content: '-';
                                    }
                                }
                            }

                            .newPrice {
                                @include RelewaySemiBold(32);
                                line-height: 40px;

                                &::after {
                                    content: ' ₽';
                                }
                            }
                        }

                        &_counterAndBtn {
                            .quantity-block {
                                display        : flex;
                                flex-direction : row;
                                align-items    : center;
                                border         : 1px solid $colorBgBlockSecondary;
                                border-radius  : 8px;
                                justify-content: space-between;
                                padding        : 12px;

                                input[type=number]::-webkit-inner-spin-button,
                                input[type=number]::-webkit-outer-spin-button {
                                    -webkit-appearance: none;
                                    -moz-appearance   : none;
                                    appearance        : none;
                                    margin            : 0;
                                }

                                /* Для Firefox */
                                input[type=number] {
                                    -moz-appearance: textfield;
                                }

                                .quantity-arrow-minus {
                                    width  : toRem(25);
                                    height : toRem(40);
                                    border : none;
                                    outline: none;
                                    color  : $colorTextDark;
                                    @include RelewaySemiBold(16);
                                    line-height: 24px;
                                    background : none;
                                }

                                .quantity-arrow-plus {
                                    width  : toRem(25);
                                    height : toRem(40);
                                    border : none;
                                    outline: none;
                                    color  : $colorTextDark;
                                    @include RelewaySemiBold(16);
                                    line-height: 24px;
                                    background : none;
                                }

                                .quantity-num {
                                    width     : toRem(45);
                                    height    : toRem(40);
                                    outline   : none;
                                    text-align: center;
                                    color     : $colorTextDark;
                                    @include RelewaySemiBold(16);
                                    line-height: 24px;
                                    border     : none;

                                }
                            }

                            &_btn {
                                display        : flex;
                                align-items    : center;
                                justify-content: center;
                                @include RelewaySemiBold(16);
                                line-height: 24px;
                            }
                        }
                    }
                }
            }
        }
    }
}