.catalogBanner {
    margin:  0 0 40px 0;
    .sectionInner {
        height         : 190px;
        background     : transparent url(../img/banner_catalog.png) 50% 50%/cover no-repeat;
        display        : flex;
        align-items    : center;
        justify-content: center;
        border-radius  : 16px;
    }

    &_text {
        @include RelewaySemiBold(32);
        line-height: 40px;
        color: $colorWhite;
    }
}